body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000000f3; /* Navbar background color */
  padding: 1rem 2rem;
}

.logo a {
  color: #fff; /* Logo text color */
  font-size: 1.5rem;
  text-decoration: none;
  font-weight: bold;
}

.nav-links {
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
}

.nav-links li {
  margin-right: 1rem;
}

.nav-links li a {
  color: #fff; /* Navbar links text color */
  text-decoration: none;
  padding: 0.5rem 1rem;
  transition: all 0.3s ease;
}

.nav-links li a:hover {
  background-color: #a5a5a7; /* Hover background color */
  border-radius: 5px;
}


.home-section {
  position: relative;
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  text-align: center;
  font-family:'Times New Roman', Times, serif;
  font-size: large;
  color: white; /* Set the text color to white for better contrast */
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://miro.medium.com/v2/resize:fit:2000/0*JpG3mgDOUWhA8MXT');
  background-size: cover;
  background-position: center;
  opacity: 0.8;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 1);
  z-index: 1;
}

.home-container {
  position: relative;
  z-index: 2;
  max-width: 900px;
  width: 100%;
  
}

.home-container h1 {
  font-size: 3rem; /* Adjust this value for larger text */
  font-weight: bold;
  margin-bottom: 1rem;
  color: #f9f9f9;
}

.home-container p {
  font-size: 1.5rem; /* Adjust this value for larger text */
  color: #ffffff;
  font-weight: bold;
}

section {
  padding: 2rem;
  text-align: center;
}

.prjsection{
  background: rgb(255, 255, 77);
}
.projects-section {
  padding: 2rem;
  text-align: center;
}

.project {
  border: 2px solid #333; 
  border-radius: 8px;     
  padding: 1rem;         
  margin: 1rem 0;   
  background: rgba(241, 241, 146, 0.932);
}

.project h3 {
  margin-top: 0;
}

.project a {
  color: #007bff;
  text-decoration: none;
}

.project a:hover {
  text-decoration: underline;
}

.about-section {
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #f9f9f9;
}

.about-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-width: 900px;
  margin: 0 auto;
  background-color: #fff;
  padding: 2rem;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  border-radius: 10px;
}

.about-text {
  flex: 1;
  padding: 1rem;
}

.about-text h2 {
  color: #333;
  margin-bottom: 1rem;
}

.about-text p {
  color: #666;
  line-height: 1.6;
  margin-bottom: 1rem;
}

@media (max-width: 768px) {
  .about-container {
    flex-direction: column;
  }

  .about-text{
    flex: unset;
    width: 100%;
  }
}.about-section {
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(135deg, #2a5da0a1, #7bd4fe93); /* Gradient background */
  min-height: 100vh; /* Ensure the section takes full viewport height */
}

.about-container {
  display: flex;
  flex-direction: column; /* Changed from row to column */
  align-items: center;
  max-width: 900px;
  margin: 0 auto;
  background-color: #fff;
  padding: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  border: 2px solid black;
}

.about-text {
  padding: 1rem;
}

.about-text h2 {
  color: #333;
  margin-bottom: 1rem;
}

.about-text p {
  color: #666;
  line-height: 1.6;
  margin-bottom: 1rem;
}

@media (max-width: 768px) {
  .about-container {
    padding: 1rem;
  }

  .about-text {
    width: 100%;
  }
}

.resume-section {
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #f9f9f9;
  min-height: 100vh;
  background-color: greenyellow;
}

.resume-container {
  max-width: 900px;
  width: 100%;
  background-color: #fff;
  padding: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  border: 2px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.resume-link a {
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: #093f04;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.resume-link a:hover {
  background-color: #85be28;
  text-decoration: none;
}

.contact-section {
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #c072c7;
  min-height: 100vh;
}

.contact-container {
  max-width: 600px;
  width: 100%;
  background-color: #d4c4d8;
  padding: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.contact-info {
  list-style-type: none;
  padding: 0;
}

.contact-info li {
  margin-bottom: 1rem;
}

.contact-info a {
  color: #007bff;
  text-decoration: none;
  transition: color 0.3s;
}

.contact-info a:hover {
  color: #0056b3;
}

h1, h2 {
  color: #333;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  margin: 0.5rem 0;
}

input, textarea {
  padding: 0.5rem;
  margin: 0.5rem 0;
  width: 100%;
  max-width: 400px;
}

button {
  padding: 0.5rem 1rem;
  background-color: #000000f3;
  color: white;
  border: none;
  cursor: pointer;
}

ul {
  list-style: none;
  padding: 0;
}

ul li {
  margin: 0.5rem 0;
}

a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

